#particles-js canvas {
  -webkit-transform: scale(1);
          transform: scale(1);
  opacity: 1;
  transition: opacity .8s ease, -webkit-transform 1.4s ease;
  transition: opacity .8s ease, transform 1.4s ease;
  transition: opacity .8s ease, transform 1.4s ease, -webkit-transform 1.4s ease;
}

#particles-js {
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: -10;
  top: 0;
  left: 0
}

#text_div {
  position: absolute;
  display: block;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  margin: 10px;
}

.text-location {
  position: absolute;
  top: 35%;
  left: 5%;
  text-align: left;
  color: white;
}

.AboutText {
  display: inline-block;
  font-size: 3.5rem;
  font-weight: 700;
}

@-webkit-keyframes textAnimation {
  0% {
    opacity: 0;
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
  }

  25% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }

  75% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }

  95% {
    opacity: 0;
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
  }

  100% {
    opacity: 0;
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
  }
}

@keyframes textAnimation {
  0% {
    opacity: 0;
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
  }

  25% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }

  75% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }

  95% {
    opacity: 0;
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
  }

  100% {
    opacity: 0;
    -webkit-transform: translateY(-30px);
            transform: translateY(-30px);
  }
}


.AboutText span.Animated-Text {
  color: #F9004D;
  color: linear-gradient(0.25turn, #F9004D, #e20148, #c7003f);
  display: inline-block;
  opacity: 0;
  font-size: 5.5rem;
  font-weight: 800;
  -webkit-animation-name: textAnimation;
          animation-name: textAnimation;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  /* animation-delay: 2s; */
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  white-space: normal;

}

