.MdClose {
    cursor: pointer;
    position: relative;
    display: block;
    top: 8px;
    right: 20px;
    width: 25px;
    height: 25px;
    padding: 0;
    margin: 0;
    z-index: 10;
    border-radius: 50%;
    background-color: rgba(255, 255, 255, 0.2);
    transition: opacity .25s, -webkit-transform .30s;
    transition: transform .30s, opacity .25s;
    transition: transform .30s, opacity .25s, -webkit-transform .30s;
}

.MdClose:hover {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
